<template>
  <!--忘记密码表单-->
  <div>
    <section class="login">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-7 col-lg-5">
            <div class="login-wrap p-3 p-md-5">
              <a-form-model ref="formGetPwd" :model="formGetPwd" :rules="rules" @submit="changePwd"
                            @submit.native.prevent>
                <div v-if="!isSuccess">
                  <a-form-model-item prop="phone">
                    <a-input v-model.number="formGetPwd.phone" placeholder="请输入绑定的手机号" size="large">
                      <a-icon slot="prefix" type="phone" style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                  </a-form-model-item>
                  <a-form-model-item prop="code">
                    <a-input-search v-model.number="formGetPwd.code" placeholder="输入验证码" size="large"
                                    @search="sendCode">
                      <a-icon slot="prefix" type="code" style="color:rgba(0,0,0,.25)"/>
                      <a-button slot="enterButton" :disabled="disable">
                        {{ btnText }}
                      </a-button>
                    </a-input-search>
                  </a-form-model-item>
                  <a-form-model-item>
                    <a-button
                        type="primary" block
                        size="large"
                        :loading="isLoadingNext"
                        @click="nextStep"
                    >
                      下 一 步
                    </a-button>
                  </a-form-model-item>
                </div>
                <div v-else>
                  <a-form-model-item prop="password">
                    <a-input v-model="formGetPwd.password" type="password" placeholder="请输入新密码" size="large">
                      <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                  </a-form-model-item>
                  <a-form-model-item prop="confirmPassword">
                    <a-input v-model="formGetPwd.confirmPassword" type="password" placeholder="请确认新密码"
                             size="large">
                      <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                  </a-form-model-item>
                  <a-form-model-item>
                    <a-button
                        type="primary" block
                        html-type="submit"
                        size="large"
                        :loading="isLoadingSubmit"
                    >
                      提 交
                    </a-button>
                  </a-form-model-item>
                </div>
              </a-form-model>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: 'Register',
    data() {
      let validatorPhone = (rule, value, callback) => {
        // 正则判断手机号格式的格式，正则判断失败抛出错误，否则直接callback()
        if (!/^1[2-9]\d{9}$/.test(value)) {
          callback(new Error("手机号格式不正确!"));
        } else {
          callback();
        }
      }

      let checkCode = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入验证码！'));
        }

        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字！'));
        } else if (!/^\d{6}$/g.test(value)) {
          callback(new Error('验证码位数不正确！'));
        } else {
          callback();
        }
      }

      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码！'));
        } else if (!/^(?=.*[a-zA-Z])(?=.*[0-9])[A-Za-z0-9]{8,16}$/.test(value)) {
          callback(new Error('新密码必须由数字和字母构成，不能全部为数字，不能全部为字母，长度在8-16位之间'));
        } else if (this.formGetPwd.confirmPassword !== '') {
          this.$refs.formGetPwd.validateField('confirmPassword');
          callback();
        } else {
          callback();
        }

      }

      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入新密码！'));
        } else if (value !== this.formGetPwd.password) {
          callback(new Error("两次输入的密码不一致!"));
        } else {
          callback();
        }
      };

      return {
        formGetPwd: {
          phone: '',
          code: '',
          password: '',
          confirmPassword: '',
        },
        isSuccess: false, //手机号验证是否成功
        disable: false, //验证码60秒倒计时是否显示
        btnText: '获取验证码',
        isLoadingNext: false, //下一步按钮的加载动画
        isLoadingSubmit: false, //提交修改密码按钮的加载动画
        rules: {
          phone: [
            {required: true, message: '请输入手机号！', trigger: 'blur'},
            {validator: validatorPhone, trigger: 'change'}
          ],
          code: [{validator: checkCode, trigger: 'change'}],
          password: [{validator: validatePass, trigger: 'change'}],
          confirmPassword: [{validator: validatePass2, trigger: 'change'}]
        }
      };
    },
    methods: {
      changePwd() {
        this.$refs.formGetPwd.validateField(['password', 'confirmPassword'], error => {
          if (error) {
            this.$message.error('请先规范填写表单！');
            return;
          }

          this.isLoadingSubmit = true;
          const {phone, password} = this.formGetPwd;
          const id = storage.get('userInfo').uid;

          request({
            method: 'patch',
            url: `/users/${id}/passwords`,
            data: {phone: phone, newPassword: password}
          }).then(res => {
            if (res.code === 1){
              storage.clearAll();
              this.$router.replace({name: 'changePwdSuccess'});
            }else {
              this.$message.error(res.msg);
            }
            this.isLoadingSubmit = false;
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
      sendCode() {
        this.$refs.formGetPwd.validateField(['phone'], error => {
          if (error) {
            this.$message.error('获取验证码失败！');
            return;
          }

          this.disable = true;
          let count = 60;
          let timer = setInterval(() => {
            this.btnText = count + ' 秒后重新发送';
            count--;
            if (count < 0) {
              this.disable = false;
              this.btnText = '获取验证码';
              clearInterval(timer);
            }
          }, 1000)

          request({
            method: 'get',
            url: '/codes',
            params: {phone: this.formGetPwd.phone}
          }).then(res => {
            if (res.code === 1) {
              this.$message.success('验证码发送成功，请注意查收！', 5);
            } else {
              this.$message.error(res.msg);
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
      nextStep() {
        this.$refs.formGetPwd.validateField(['phone', 'code'], error => {
              if (error) {
                this.$message.error('请先规范填写表单！');
                return;
              }

              this.isLoadingNext = true;
              const {phone, code} = this.formGetPwd;

              request({
                method: 'get',
                url: '/check',
                params: {phone: phone, code: code}
              }).then(res => {
                if (res.code === 1) {
                  this.isSuccess = true;
                } else {
                  this.$message.error(res.msg);
                }
              }).catch(error => {
                this.$message.error('服务器错误！');
                console.log(error);
              })
            }
        )
      }
    },
    activated() {
      this.isLoadingNext = false;
      this.isSuccess = false;
    }
  }
</script>

<style scoped>
  .login {
    min-height: 720px;
    background-color: #f8f9fd;
  }

  .login-wrap {
    margin-top: 23%;
    position: relative;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 34px -15px rgb(0 0 0 / 24%);
  }

  .login-wrap h3 {
    font-weight: 350;
  }


  .col-lg-5 {
    flex: 0 0 auto;
    width: 35%;
  }


  .col h4 {
    text-align: center;
  }

  .active h4 {
    color: green;
    border-bottom: 2px solid green;
  }

  .ant-form {
    margin-top: 5%;
  }

  /deep/ .ant-input-lg {
    height: 50px;
  }

  .ant-btn-lg {
    height: 48px;
  }


  .form-group {
    margin-bottom: 1rem;
  }

  .ant-input-search-button {
    height: 50px;
  }

</style>